const WXAPI = require('apifm-wxapi')
WXAPI.init('tae')
export default {
	data() {
		return {
			hideEyes: false,
			form:{
				username:"",
				pwd:""
			}
		}
		},
	methods: {
		passwordF_B() {
			this.hideEyes = !this.hideEyes;
		},
		formSubmit() {
			wx.login({
			 success: function (res) {
			 const code = res.code; // 微信登录接口返回的 code 参数，下面登录接口需要用到
			 WXAPI.login_wx(code).then(function (res) {
			 // 登录接口返回结果
			     console.log(res)
				if(res.msg=="success"){
					try{
						uni.setStorageSync('token',res.data)
						uni.showToast({
							title:"登录成功"
						})
						setTimeout(function() {
							uni.switchTab({
								url:"../user/user"
							})
						}, 2000);
					}catch(e){}	
				}
			 })
			 }
		 })
		},
		register(){
			uni.navigateTo({
				url:"../register/register"
			})
		}
	}
}
